import Web3 from "web3";
import {useSelector} from "react-redux";
import {useEffect, useState} from "react";
import {makeBatchRequest} from "../utils/promisify";

import farm_abi from "../abis/MultiRewards.json";
import ierc20_abi from "../abis/IERC20.json";
import {formatWei, formatWeiToNumber, toWei} from "../utils/format";
import CoineusLoading from "../components/CoineusLoading";
import {
    Avatar, AvatarGroup,
    Button,
    FormControl,
    Grid, InputAdornment,
    List,
    ListItem, ListItemAvatar,
    ListItemSecondaryAction,
    ListItemText,
    Paper,
    TextField,
    Typography
} from "@mui/material";
import {CoineusCryptoFormat} from "../utils/currency_format";
import {NetworkButton} from "../components/Coineus";
import {CHAIN_IDS} from "../constants";
import BigNumber from "bignumber.js";
import PercentageArc from "../components/PercentageArc";
import {RewardDetails} from "./CHADBNBStaking";

const OLD_FARM_CONTRACT = "0x7243265f91e138a11548DF4253E3ec056b6348f8";
// const FARM_CONTRACT = "0xF0c7a373A653D4Fe1004d6D3f5FF45Ff4A06FB66";
const FARM_CONTRACT = "0xC2B83a3C3199Fb29Ce1d8Dfc681001AF3e97e004";

const STAKE_TOKEN = "0x87440F829E7ED808599749630555080917c4b3D9";

const REWARD_TOKEN0 = "0x4e69Ae0CD024754655b4eF74F24A8DCB39Ba07e8";//CEUS
const REWARD_TOKEN1 = "0xF2C6C1AA2bf8ec40F564Ea8A483F64907ea37A3F";//LIT
const REWARD_TOKEN2 = "0xd0ce1b4A349C35e61Af02f5971e71ac502441E49";//fUSD
// 0xd0ce1b4A349C35e61Af02f5971e71ac502441E49

const updateTimeout = 10000;

export default function Farm() {

    const {wallet, rpc} = useSelector(state => state.coineus);

    const web3 = new Web3(rpc.fuse);
    const farm_contract = new web3.eth.Contract(farm_abi, FARM_CONTRACT);
    const stake_token = new web3.eth.Contract(ierc20_abi, STAKE_TOKEN)

    const [data, setData] = useState({});
    const [pending, setPending] = useState(false);
    const [amount, setAmount] = useState("");

    const getInfo = () => {

        if (!wallet.address) return;

        const calls = [
            farm_contract.methods.totalSupply().call,
            farm_contract.methods.balanceOf(wallet.address).call,

            stake_token.methods.balanceOf(wallet.address).call,
            stake_token.methods.totalSupply().call,

            //reward 0
            farm_contract.methods.rewardData(REWARD_TOKEN0).call,
            farm_contract.methods.earned(wallet.address, REWARD_TOKEN0).call,
            farm_contract.methods.getRewardForDuration(REWARD_TOKEN0).call,

            //reward 1
            farm_contract.methods.rewardData(REWARD_TOKEN1).call,
            farm_contract.methods.earned(wallet.address, REWARD_TOKEN1).call,
            farm_contract.methods.getRewardForDuration(REWARD_TOKEN1).call,

            //reward 2
            farm_contract.methods.rewardData(REWARD_TOKEN2).call,
            farm_contract.methods.earned(wallet.address, REWARD_TOKEN2).call,
            farm_contract.methods.getRewardForDuration(REWARD_TOKEN2).call,
        ]

        makeBatchRequest(web3, calls).then(resp => {
            const [
                totalStaked,
                stakedAmount,

                stake_token_balance,
                stake_token_supply,

                rewardTokenData0,
                earned0,
                getRewardForDuration0,

                rewardTokenData1,
                earned1,
                getRewardForDuration1,

                rewardTokenData2,
                earned2,
                getRewardForDuration2,
            ] = resp;

            const secondsLeft0 = calcSecsLeft(rewardTokenData0.periodFinish);
            const rewardsRemaining0 = secondsLeft0 * rewardTokenData0.rewardRate / (10 ** 18);
            const potentialEarnings0 = rewardsRemaining0 * (stakedAmount / totalStaked);

            const secondsLeft1 = calcSecsLeft(rewardTokenData1.periodFinish);
            const rewardsRemaining1 = secondsLeft1 * rewardTokenData1.rewardRate / (10 ** 18);
            const potentialEarnings1 = rewardsRemaining1 * (stakedAmount / totalStaked);

            const secondsLeft2 = calcSecsLeft(rewardTokenData2.periodFinish);
            const rewardsRemaining2 = secondsLeft2 * rewardTokenData2.rewardRate / (10 ** 18);
            const potentialEarnings2 = rewardsRemaining2 * (stakedAmount / totalStaked);

            setData({
                stake_token_supply: stake_token_supply / (10 ** 18),
                stake_token_balance: formatWeiToNumber(stake_token_balance, 18),
                totalStaked: formatWeiToNumber(totalStaked, 18),
                stakedAmount: formatWeiToNumber(stakedAmount, 18),

                reward0: {
                    contract: REWARD_TOKEN0,
                    pending: formatWeiToNumber(earned0, 18),
                    rewardsForDuration: formatWeiToNumber(getRewardForDuration0, 18),
                    rewardsRemaining: rewardsRemaining0,
                    potentialEarnings: potentialEarnings0,
                    periodFinish: rewardTokenData0.periodFinish * 1000
                },

                reward1: {
                    contract: REWARD_TOKEN1,
                    pending: formatWeiToNumber(earned1, 18),
                    rewardsForDuration: formatWeiToNumber(getRewardForDuration1, 18),
                    rewardsRemaining: rewardsRemaining1,
                    potentialEarnings: potentialEarnings1,
                    periodFinish: rewardTokenData1.periodFinish * 1000
                },

                reward2: {
                    contract: REWARD_TOKEN2,
                    pending: formatWeiToNumber(earned2, 18),
                    rewardsForDuration: formatWeiToNumber(getRewardForDuration2, 18),
                    rewardsRemaining: rewardsRemaining2,
                    potentialEarnings: potentialEarnings2,
                    periodFinish: rewardTokenData2.periodFinish * 1000
                }
            });

        })
    }

    const calcSecsLeft = (periodFinish) => {
        const left = parseInt(((periodFinish * 1000) - new Date().getTime()) / 1000);

        return left > 0 ? left : 0;
    }

    /**
     * addRewards(rewardToken, managerAddress, duration) - add reward token
     * 2592000 = 30 days
     * 604800 = 7 days
     */

    const stake = () => {
        if (wallet.address) {
            const web3 = new Web3(window.provider);

            const stake_token_contract = new web3.eth.Contract(ierc20_abi, STAKE_TOKEN);
            const farm_contract = new web3.eth.Contract(farm_abi, FARM_CONTRACT);

            stake_token_contract
                .methods
                .allowance(wallet.address, FARM_CONTRACT)
                .call((err, resp) => {

                    if (err) return;

                    if (parseInt(resp) < parseInt(toWei(amount, 18))) {
                        stake_token_contract
                            .methods
                            .approve(FARM_CONTRACT, toWei(amount, 18))
                            .send({from: wallet.address})
                            .once('transactionHash', function (tx) {
                                setPending(true)
                            })
                            .on('error', function (error) {
                                setPending(false)
                            })
                            .then(function (receipt) {
                                setPending(false)
                                stake()
                            });
                    } else {
                        stake()
                    }
                })

            const stake = () => {
                farm_contract.methods.stake(toWei(amount, 18))
                    .send({from: wallet.address, gas: 700000})
                    .once('transactionHash', function (tx) {
                        setPending(true)
                    })
                    .on('error', function (error) {
                        setPending(false)
                    })
                    .then(function (receipt) {
                        setPending(false);
                        getInfo();
                    });
            }
        }
    }

    const getReward = () => {
        if (wallet.address) {
            const web3 = new Web3(window.provider);
            const farm_contract = new web3.eth.Contract(farm_abi, FARM_CONTRACT);
            farm_contract.methods.getReward()
                .send({from: wallet.address})
                .once('transactionHash', function (tx) {
                    setPending(true)
                })
                .on('error', function (error) {
                    setPending(false)
                })
                .then(function (receipt) {
                    getInfo();
                    setPending(false);
                });
        }
    }

    const exit = () => {
        if (wallet.address) {
            const web3 = new Web3(window.provider);
            const farm_contract = new web3.eth.Contract(farm_abi, FARM_CONTRACT);
            farm_contract.methods.exit()
                .send({from: wallet.address})
                .once('transactionHash', function (tx) {
                    setPending(true)
                })
                .on('error', function (error) {
                    setPending(false)
                })
                .then(function (receipt) {
                    getInfo();
                    setPending(false);
                });
        }
    }

    useEffect(() => {
        getInfo();
        const interval = setInterval(getInfo, updateTimeout)
        return () => clearInterval(interval);
    }, [wallet.address]);

    return <Grid container spacing={2} style={{marginTop: -5, paddingBottom: 20}}>
        <Grid item xs={12}>
            <Paper>
                <List>
                    <ListItem>
                        <ListItemAvatar style={{marginRight:10}}>
                            <AvatarGroup>
                                <Avatar src={`https://coineus.app/assets/tokens/fuse/0x4e69Ae0CD024754655b4eF74F24A8DCB39Ba07e8/logo.png`}/>
                                <Avatar src={`https://coineus.app/assets/tokens/fuse/0xF2C6C1AA2bf8ec40F564Ea8A483F64907ea37A3F/logo.png`}/>
                            </AvatarGroup>
                        </ListItemAvatar>
                        <ListItemText
                            primary={CoineusCryptoFormat(data.totalStaked)}
                            secondary="Total CEUS/LIT LP Staked"
                        />
                        <ListItemSecondaryAction>
                            <Button href="https://app.voltage.finance/#/add/0x4e69ae0cd024754655b4ef74f24a8dcb39ba07e8/0xf2c6c1aa2bf8ec40f564ea8a483f64907ea37a3f" target="_blank">Add LP</Button>
                        </ListItemSecondaryAction>
                    </ListItem>

                    <ListItem>
                        <ListItemText
                            primary={CoineusCryptoFormat(data.stakedAmount)}
                            secondary="My Staked Amount"
                        />

                        <div style={{marginTop: -15}}>
                            <PercentageArc
                                value={data.stakedAmount}
                                total={data.totalStaked}
                                height={50}
                                label="MY STAKE SHARE"
                            />
                        </div>
                    </ListItem>

                    <ListItem>
                        <NetworkButton network={CHAIN_IDS.FUSE}>
                            <Button
                                variant="contained"
                                fullWidth
                                onClick={() => exit()}>Withdraw</Button>
                        </NetworkButton>
                    </ListItem>

                    <ListItem>
                        <FormControl fullWidth style={{marginBottom: 20, marginTop: 20}}>
                            <TextField
                                autoComplete="off"
                                type="number"
                                value={amount}
                                placeholder="Enter Amount"
                                onChange={(ev) => {
                                    setAmount(ev.target.value)
                                }}
                                helperText={`CEUS/LIT LP Balance: ${data.stake_token_balance}`}
                                InputProps={{
                                    endAdornment: <InputAdornment position="end">
                                        <Button variant="contained" onClick={() => {
                                            setAmount(data.stake_token_balance - (1/(10**12)))
                                        }
                                        }>MAX</Button>
                                    </InputAdornment>,
                                }}
                            />
                        </FormControl>
                    </ListItem>

                    <ListItem>
                        <NetworkButton network={CHAIN_IDS.FUSE}>
                            <Button
                                variant="contained"
                                fullWidth
                                onClick={() => stake()}>STAKE</Button>
                        </NetworkButton>
                    </ListItem>

                </List>
            </Paper>
        </Grid>

        <RewardDetails
            details={data.reward0}
            rewardPriceInUSD={wallet.prices.fuse?.[data.reward0?.contract]?.inUSD}
            rewardSymbol="CEUS"
            network="fuse"
        />

        <RewardDetails
            details={data.reward1}
            rewardPriceInUSD={wallet.prices.fuse?.[data.reward1?.contract]?.inUSD}
            rewardSymbol="LIT"
            network="fuse"
        />

        <RewardDetails
            details={data.reward2}
            rewardPriceInUSD={wallet.prices.fuse?.[data.reward2?.contract]?.inUSD}
            rewardSymbol="fUSD"
            network="fuse"
        />

        <Grid item xs={12}>
            <Paper>
                <List>
                    <ListItem>
                        <NetworkButton network={CHAIN_IDS.FUSE}>
                            <Button
                                variant="contained"
                                fullWidth
                                onClick={() => getReward()}>Claim Rewards</Button>
                        </NetworkButton>
                    </ListItem>
                </List>
            </Paper>
        </Grid>


        <CoineusLoading open={pending} label="Transaction Pending"/>
    </Grid>
}