import Web3 from "web3";
import {useSelector} from "react-redux";
import {useEffect, useState} from "react";
import {makeBatchRequest} from "../utils/promisify";

import farm_abi from "../abis/MultiRewards.json";
import ierc20_abi from "../abis/IERC20.json";
import {formatWeiToNumber, toWei} from "../utils/format";
import CoineusLoading from "../components/CoineusLoading";
import {
    Avatar,
    Button,
    FormControl,
    Grid, InputAdornment,
    List,
    ListItem, ListItemAvatar,
    ListItemText,
    Paper,
    TextField
} from "@mui/material";
import {CoineusCryptoFormat, CoineusUSDFormat} from "../utils/currency_format";
import {NetworkButton} from "../components/Coineus";
import {CHAIN_IDS} from "../constants";
import PercentageArc from "../components/PercentageArc";
import AnimatedPriceWidget from "../components/AnimatedPriceWidget";

// const FARM_CONTRACT = "0x678266b93f77d36756db35166967C213fD0b5aE6";
const FARM_CONTRACT = "0xF793a9803c1260C470858cd20D75E37e5BF3C999";
const STAKE_TOKEN = "0xcC8A1364Bd05e05402e8b28dAe554b5d6216E6B9";//CHAD
const REWARD_TOKEN0 = "0x9f19c8e321bD14345b797d43E01f0eED030F5Bff";//SRG
const REWARD_TOKEN1 = "0xbC8F89861737C3B8c60a05165E0a5C5B8A137fAc";//LIT
const REWARD_TOKEN2 = "0x67B38309BACBc77E6570e9557b23d6955A9bD7e3";//PEPE
const REWARD_TOKEN3 = "0x5857c96DaE9cF8511B08Cb07f85753C472D36Ea3";//FUSE on BSC

const updateTimeout = 10000;

export default function CHADBNBStaking() {

    const {wallet, rpc} = useSelector(state => state.coineus);

    const web3 = new Web3(rpc.bnb);
    const farm_contract = new web3.eth.Contract(farm_abi, FARM_CONTRACT);
    const stake_token = new web3.eth.Contract(ierc20_abi, STAKE_TOKEN)

    const [data, setData] = useState({});
    const [pending, setPending] = useState(false);
    const [amount, setAmount] = useState("");
    // const [apy, setApy] = useState("");

    const getInfo = () => {

        if (!wallet.address) return;

        const calls = [
            farm_contract.methods.totalSupply().call,
            farm_contract.methods.balanceOf(wallet.address).call,

            stake_token.methods.balanceOf(wallet.address).call,
            stake_token.methods.totalSupply().call,

            //reward 0
            farm_contract.methods.rewardData(REWARD_TOKEN0).call,
            farm_contract.methods.earned(wallet.address, REWARD_TOKEN0).call,
            farm_contract.methods.getRewardForDuration(REWARD_TOKEN0).call,

            //reward 1
            farm_contract.methods.rewardData(REWARD_TOKEN1).call,
            farm_contract.methods.earned(wallet.address, REWARD_TOKEN1).call,
            farm_contract.methods.getRewardForDuration(REWARD_TOKEN1).call,

            //reward 2
            farm_contract.methods.rewardData(REWARD_TOKEN2).call,
            farm_contract.methods.earned(wallet.address, REWARD_TOKEN2).call,
            farm_contract.methods.getRewardForDuration(REWARD_TOKEN2).call,

            //reward 3
            farm_contract.methods.rewardData(REWARD_TOKEN3).call,
            farm_contract.methods.earned(wallet.address, REWARD_TOKEN3).call,
            farm_contract.methods.getRewardForDuration(REWARD_TOKEN3).call
        ]

        makeBatchRequest(web3, calls).then(resp => {
            const [
                totalStaked,
                stakedAmount,

                stake_token_balance,
                stake_token_supply,

                rewardTokenData0,
                earned0,
                getRewardForDuration0,

                rewardTokenData1,
                earned1,
                getRewardForDuration1,

                rewardTokenData2,
                earned2,
                getRewardForDuration2,

                rewardTokenData3,
                earned3,
                getRewardForDuration3
            ] = resp;

            // const totalRewards = getRewardForDuration0;
            // const rewardPrice = wallet.prices.bnb?.[REWARD_TOKEN0]?.inUSD; //SRG Price
            // const totalRewardsInUSD = formatWeiToNumber(totalRewards, 9) * rewardPrice
            //
            // const durationInDays = rewardTokenData0.rewardsDuration / (3600 * 24)
            // const apyPercent = ((totalRewardsInUSD / totalStaked) * (365 / durationInDays))
            // setApy(apyPercent);

            const secondsLeft0 = calcSecsLeft(rewardTokenData0.periodFinish);
            const rewardsRemaining0 = secondsLeft0 * rewardTokenData0.rewardRate / (10 ** 9);
            const potentialEarnings0 = rewardsRemaining0 * (stakedAmount / totalStaked);

            const secondsLeft1 = calcSecsLeft(rewardTokenData1.periodFinish);
            const rewardsRemaining1 = secondsLeft1 * rewardTokenData1.rewardRate / (10 ** 9);
            const potentialEarnings1 = rewardsRemaining1 * (stakedAmount / totalStaked);

            const secondsLeft2 = calcSecsLeft(rewardTokenData2.periodFinish);
            const rewardsRemaining2 = secondsLeft2 * rewardTokenData2.rewardRate / (10 ** 9);
            const potentialEarnings2 = rewardsRemaining2 * (stakedAmount / totalStaked);

            const secondsLeft3 = calcSecsLeft(rewardTokenData3.periodFinish);
            const rewardsRemaining3 = secondsLeft3 * rewardTokenData3.rewardRate / (10 ** 18);
            const potentialEarnings3 = rewardsRemaining3 * (stakedAmount / totalStaked);


            setData({
                stake_token_supply: stake_token_supply / (10 ** 9),
                stake_token_balance: formatWeiToNumber(stake_token_balance, 9),
                totalStaked: formatWeiToNumber(totalStaked, 9),
                stakedAmount: formatWeiToNumber(stakedAmount, 9),

                reward0: {
                    contract: REWARD_TOKEN0,
                    pending: formatWeiToNumber(earned0, 9),
                    rewardsForDuration: formatWeiToNumber(getRewardForDuration0, 9),
                    rewardsRemaining: rewardsRemaining0,
                    potentialEarnings: potentialEarnings0,
                    periodFinish: rewardTokenData0.periodFinish * 1000
                },

                reward1: {
                    contract: REWARD_TOKEN1,
                    pending: formatWeiToNumber(earned1, 9),
                    rewardsForDuration: formatWeiToNumber(getRewardForDuration1, 9),
                    rewardsRemaining: rewardsRemaining1,
                    potentialEarnings: potentialEarnings1,
                    periodFinish: rewardTokenData1.periodFinish * 1000
                },

                reward2: {
                    contract: REWARD_TOKEN2,
                    pending: formatWeiToNumber(earned2, 9),
                    rewardsForDuration: formatWeiToNumber(getRewardForDuration2, 9),
                    rewardsRemaining: rewardsRemaining2,
                    potentialEarnings: potentialEarnings2,
                    periodFinish: rewardTokenData2.periodFinish * 1000
                },

                reward3: {
                    contract: REWARD_TOKEN3,
                    pending: formatWeiToNumber(earned3, 18),
                    rewardsForDuration: formatWeiToNumber(getRewardForDuration3, 18),
                    rewardsRemaining: rewardsRemaining3,
                    potentialEarnings: potentialEarnings3,
                    periodFinish: rewardTokenData3.periodFinish * 1000
                }
            });

        })
    }

    const calcSecsLeft = (periodFinish) => {
        const left = parseInt(((periodFinish * 1000) - new Date().getTime()) / 1000);

        return left > 0 ? left : 0;
    }

    /**
     * addRewards(rewardToken, managerAddress, duration) - add reward token
     * 2592000 = 30 days
     * 604800 = 7 days
     */

    const stake = () => {
        if (wallet.address) {
            const web3 = new Web3(window.provider);

            const stake_token_contract = new web3.eth.Contract(ierc20_abi, STAKE_TOKEN);
            const farm_contract = new web3.eth.Contract(farm_abi, FARM_CONTRACT);

            stake_token_contract
                .methods
                .allowance(wallet.address, FARM_CONTRACT)
                .call((err, resp) => {

                    if (err) return;

                    if (parseInt(resp) < parseInt(toWei(amount, 9))) {
                        stake_token_contract
                            .methods
                            .approve(FARM_CONTRACT, toWei(amount, 9))
                            .send({from: wallet.address})
                            .once('transactionHash', function (tx) {
                                setPending(true)
                            })
                            .on('error', function (error) {
                                setPending(false)
                            })
                            .then(function (receipt) {
                                setPending(false)
                                stake()
                            });
                    } else {
                        stake()
                    }
                })

            const stake = () => {
                farm_contract.methods.stake(toWei(amount, 9))
                    .send({from: wallet.address, gas: 700000})
                    .once('transactionHash', function (tx) {
                        setPending(true)
                    })
                    .on('error', function (error) {
                        setPending(false)
                    })
                    .then(function (receipt) {
                        setPending(false);
                        getInfo();
                    });
            }
        }
    }

    const getReward = () => {
        if (wallet.address) {
            const web3 = new Web3(window.provider);
            const farm_contract = new web3.eth.Contract(farm_abi, FARM_CONTRACT);
            farm_contract.methods.getReward()
                .send({from: wallet.address})
                .once('transactionHash', function (tx) {
                    setPending(true)
                })
                .on('error', function (error) {
                    setPending(false)
                })
                .then(function (receipt) {
                    getInfo();
                    setPending(false);
                });
        }
    }

    const exit = () => {
        if (wallet.address) {
            const web3 = new Web3(window.provider);
            const farm_contract = new web3.eth.Contract(farm_abi, FARM_CONTRACT);
            farm_contract.methods.exit()
                .send({from: wallet.address})
                .once('transactionHash', function (tx) {
                    setPending(true)
                })
                .on('error', function (error) {
                    setPending(false)
                })
                .then(function (receipt) {
                    getInfo();
                    setPending(false);
                });
        }
    }

    useEffect(() => {
        getInfo();
        const interval = setInterval(getInfo, updateTimeout)
        return () => clearInterval(interval);
    }, [wallet.address]);

    return <Grid container spacing={2} style={{marginTop: -5, paddingBottom: 20}}>
        <Grid item xs={12}>
            <Paper>
                <List>
                    <ListItem>
                        <ListItemAvatar>
                            <Avatar src={`https://coineus.app/assets/tokens/bnb/${STAKE_TOKEN}/logo.png`}/>
                        </ListItemAvatar>
                        <ListItemText
                            primary={CoineusCryptoFormat(data.totalStaked)}
                            secondary="Total CHAD Staked"
                        />
                        <div style={{marginTop: -15}}>
                            <PercentageArc
                                value={data.totalStaked}
                                total={data.stake_token_supply}
                                height={50}
                                label="% SUPPLY STAKED"
                            />
                        </div>
                    </ListItem>
                    <ListItem>
                        <ListItemAvatar />
                        <ListItemText
                            primary={CoineusCryptoFormat(data.stakedAmount)}
                            secondary="My Staked Amount"
                        />

                        <div style={{marginTop: -15}}>
                            <PercentageArc
                                value={data.stakedAmount}
                                total={data.totalStaked}
                                height={50}
                                label="MY STAKE SHARE"
                            />
                        </div>
                    </ListItem>
                    <ListItem>
                            <NetworkButton network={CHAIN_IDS.BNB}>
                                <Button
                                    variant="contained"
                                    fullWidth
                                    onClick={() => exit()}>Withdraw</Button>
                            </NetworkButton>
                    </ListItem>
                    <ListItem>
                        <FormControl fullWidth style={{marginBottom: 20, marginTop: 20}}>
                            <TextField
                                autoComplete="off"
                                type="number"
                                value={amount}
                                placeholder="Enter Amount"
                                onChange={(ev) => {
                                    setAmount(ev.target.value)
                                }}
                                helperText={`CHAD Balance: ${data.stake_token_balance}`}
                                InputProps={{
                                    endAdornment: <InputAdornment position="end">
                                        <Button variant="contained" onClick={() => {
                                            setAmount(data.stake_token_balance.toFixed(8))
                                        }
                                        }>MAX</Button>
                                    </InputAdornment>,
                                }}
                            />
                        </FormControl>
                    </ListItem>
                    <ListItem>
                        <NetworkButton network={CHAIN_IDS.BNB}>
                            <Button
                                variant="contained"
                                fullWidth
                                onClick={() => stake()}>STAKE</Button>
                        </NetworkButton>
                    </ListItem>
                </List>
            </Paper>
        </Grid>

        <RewardDetails
            details={data.reward3}
            rewardPriceInUSD={wallet.prices.bnb?.[data.reward3?.contract]?.inUSD}
            rewardSymbol="FUSE"
            network="bnb"
        />

        <RewardDetails
            details={data.reward0}
            rewardPriceInUSD={wallet.prices.bnb?.[data.reward0?.contract]?.inUSD}
            rewardSymbol="SRG"
            network="bnb"
        />

        <RewardDetails
            details={data.reward1}
            rewardPriceInUSD={wallet.prices.bnb?.[data.reward1?.contract]?.inUSD}
            rewardSymbol="LIT"
            network="bnb"
        />

        <RewardDetails
            details={data.reward2}
            rewardPriceInUSD={wallet.prices.bnb?.[data.reward2?.contract]?.inUSD}
            rewardSymbol="PEPE"
            network="bnb"
        />

        <Grid item xs={12}>
            <Paper>
                <List>
                    <ListItem>
                        <NetworkButton network={CHAIN_IDS.BNB}>
                            <Button
                                variant="contained"
                                fullWidth
                                onClick={() => getReward()}>Claim Rewards</Button>
                        </NetworkButton>
                    </ListItem>
                </List>
            </Paper>
        </Grid>

        <CoineusLoading open={pending} label="Transaction Pending"/>
    </Grid>
}

export const RewardDetails = (props) => {

    const {details, rewardPriceInUSD, rewardSymbol, network} = props;

    if(details === undefined)return <></>;

    const {
        contract,
        pending,
        rewardsForDuration,
        rewardsRemaining,
        potentialEarnings,
        periodFinish
    } = details;

    return <Grid item xs={12}>
        <Paper>
            <List>
                <ListItem>
                    <ListItemAvatar>
                        <Avatar src={`https://coineus.app/assets/tokens/${network}/${contract}/logo.png`}/>
                    </ListItemAvatar>
                    <ListItemText
                        primary={CoineusCryptoFormat(rewardsForDuration) + ` ${rewardSymbol}`}
                        secondary="Rewards"
                    />
                    <div style={{marginTop: -15}}>
                        <PercentageArc
                            value={rewardsRemaining}
                            total={rewardsForDuration}
                            height={50}
                            label="% REWARDS REMAINING"
                        />
                    </div>
                </ListItem>
                <ListItem>
                    <ListItemText
                        primary={<>
                            <AnimatedPriceWidget fixed={8} time={updateTimeout} price={potentialEarnings} />
                        </>}
                        secondary="Potential Rewards"
                        primaryTypographyProps={{fontFamily: "monospace"}}
                    />
                    <ListItemText
                        primary={CoineusUSDFormat(potentialEarnings * rewardPriceInUSD)}
                        secondary="Value"
                        primaryTypographyProps={{textAlign: "right"}}
                        secondaryTypographyProps={{textAlign: "right"}}
                    />
                </ListItem>
                <ListItem>
                    <ListItemText
                        primary={<>
                            <AnimatedPriceWidget fixed={8} time={updateTimeout} price={pending} />
                        </>}
                        primaryTypographyProps={{fontFamily: "monospace"}}
                        secondary="Earned Rewards"

                    />
                    <ListItemText
                        primary={CoineusUSDFormat(pending * rewardPriceInUSD)}
                        secondary="Value"
                        primaryTypographyProps={{textAlign: "right"}}
                        secondaryTypographyProps={{textAlign: "right"}}

                    />

                </ListItem>
            </List>
        </Paper>
    </Grid>
}