import Web3 from "web3";
import {useSelector} from "react-redux";
import {useEffect, useState} from "react";
import {makeBatchRequest} from "../utils/promisify";

import farm_abi from "../abis/MultiRewards.json";
import ierc20_abi from "../abis/IERC20.json";
import {formatWeiToNumber, toWei} from "../utils/format";
import CoineusLoading from "../components/CoineusLoading";
import {
    Button,
    FormControl,
    Grid, InputAdornment,
    List,
    ListItem,
    ListItemSecondaryAction,
    ListItemText,
    Paper,
    TextField,
    Typography
} from "@mui/material";
import {CoineusCryptoFormat} from "../utils/currency_format";
import {NetworkButton} from "../components/Coineus";
import {CHAIN_IDS} from "../constants";

const FARM_CONTRACT = "0x7653DEcd7C9057AE0af3d82a9BF382dB449c5d78";
const REWARD_TOKEN1 = "0x9f19c8e321bD14345b797d43E01f0eED030F5Bff";

export default function CHADBNBStakingOLD() {

    const {wallet, rpc} = useSelector(state => state.coineus);

    const web3 = new Web3(rpc.bnb);
    const farm_contract = new web3.eth.Contract(farm_abi, FARM_CONTRACT);

    const [data, setData] = useState({});
    const [pending, setPending] = useState(false);
    const [amount, setAmount] = useState("");
    const [allowance, setAllowance] = useState("");
    const [apy, setApy] = useState("");

    const getInfo = () => {

        if (!wallet.address) return;

        const calls = [
            farm_contract.methods.stakingToken().call,
            farm_contract.methods.totalSupply().call,
            farm_contract.methods.balanceOf(wallet.address).call,
            farm_contract.methods.rewardData(REWARD_TOKEN1).call,
            farm_contract.methods.rewards(wallet.address, REWARD_TOKEN1).call,
            farm_contract.methods.userRewardPerTokenPaid(wallet.address, REWARD_TOKEN1).call,
            farm_contract.methods.earned(wallet.address, REWARD_TOKEN1).call,
            farm_contract.methods.rewardPerToken(REWARD_TOKEN1).call,
            farm_contract.methods.getRewardForDuration(REWARD_TOKEN1).call,
            farm_contract.methods.rewardTokens(0).call
        ]

        makeBatchRequest(web3, calls).then(resp => {
            const [
                sToken,
                totalStaked,
                stakedAmount,
                rewardToken1Data,
                rewards1,
                userRewardPerTokenPaid1,
                earned1,
                rewardPerToken1,
                getRewardForDuration1,
                rewardTokens1
            ] = resp;

            console.log({
                sToken,
                totalStaked,
                stakedAmount,
                rewardToken1Data,
                rewards1,
                userRewardPerTokenPaid1,
                earned1,
                rewardPerToken1,
                getRewardForDuration1,
                rewardTokens1})

            const stake_token = new web3.eth.Contract(ierc20_abi, sToken)

            const calls = [
                stake_token.methods.balanceOf(wallet.address).call,
                stake_token.methods.allowance(wallet.address, FARM_CONTRACT).call
            ];

            makeBatchRequest(web3, calls).then(resp => {

                const [
                    stake_token_balance,
                    allowance
                ] = resp;

                const totalRewards = getRewardForDuration1;
                const rewardPrice = wallet.prices.bnb?.[REWARD_TOKEN1]?.inUSD; //SRG Price
                const totalRewardsInUSD = formatWeiToNumber(totalRewards, 9) * rewardPrice

                const durationInDays = rewardToken1Data.rewardsDuration / (3600 * 24)
                const apyPercent = ((totalRewardsInUSD / totalStaked) * (365 / durationInDays))
                setApy(apyPercent);

                setAllowance(allowance);

                const secondsLeft = parseInt(((rewardToken1Data.periodFinish * 1000) - new Date().getTime()) / 1000);
                const rewardsRemaining = secondsLeft * rewardToken1Data.rewardRate / (10 ** 9);
                const potentialEarnings = rewardsRemaining * (stakedAmount / totalStaked);

                setData({
                    stake_token: sToken,
                    stake_token_balance: formatWeiToNumber(stake_token_balance, 9),
                    totalStaked: formatWeiToNumber(totalStaked, 9),
                    stakedAmount: formatWeiToNumber(stakedAmount, 9),
                    rewardToken1Data,
                    rewards1: formatWeiToNumber(rewards1, 9),
                    userRewardPerTokenPaid1: formatWeiToNumber(userRewardPerTokenPaid1, 9),
                    earned1: formatWeiToNumber(earned1, 9),
                    rewardPerToken1: formatWeiToNumber(rewardPerToken1, 9),
                    getRewardForDuration1: formatWeiToNumber(getRewardForDuration1, 9),
                    rewardTokens1,
                    rewardsRemaining,
                    potentialEarnings
                })
            })

        })
    }

    /**
     * addRewards(rewardToken, managerAddress, duration) - add reward token
     * 2592000 = 30 days
     */

    const stake = () => {
        if (wallet.address) {
            const web3 = new Web3(window.provider);

            const stake_token_contract = new web3.eth.Contract(ierc20_abi, data.stake_token);
            const farm_contract = new web3.eth.Contract(farm_abi, FARM_CONTRACT);


            stake_token_contract
                .methods
                .allowance(wallet.address, FARM_CONTRACT)
                .call((err, resp) => {

                    if (err) return;

                    if (resp < toWei(amount,9)) {
                        stake_token_contract
                            .methods
                            .approve(FARM_CONTRACT, toWei(amount,9))
                            .send({from: wallet.address})
                            .once('transactionHash', function (tx) {
                                setPending(true)
                            })
                            .on('error', function (error) {
                                setPending(false)
                            })
                            .then(function (receipt) {
                                setPending(false)
                                stake()
                            });
                    } else {
                        stake()
                    }
                })

            const stake = () => {
                farm_contract.methods.stake(toWei(amount,9))
                    .send({from: wallet.address, gas: 700000})
                    .once('transactionHash', function (tx) {
                        setPending(true)
                    })
                    .on('error', function (error) {
                        setPending(false)
                    })
                    .then(function (receipt) {
                        getInfo();
                        setPending(false);
                    });
            }
        }
    }

    const getReward = () => {
        if (wallet.address) {
            const web3 = new Web3(window.provider);
            const farm_contract = new web3.eth.Contract(farm_abi, FARM_CONTRACT);
            farm_contract.methods.getReward()
                .send({from: wallet.address})
                .once('transactionHash', function (tx) {
                    setPending(true)
                })
                .on('error', function (error) {
                    setPending(false)
                })
                .then(function (receipt) {
                    getInfo();
                    setPending(false);
                });
        }
    }

    const exit = () => {
        if (wallet.address) {
            const web3 = new Web3(window.provider);
            const farm_contract = new web3.eth.Contract(farm_abi, FARM_CONTRACT);
            farm_contract.methods.exit()
                .send({from: wallet.address})
                .once('transactionHash', function (tx) {
                    setPending(true)
                })
                .on('error', function (error) {
                    setPending(false)
                })
                .then(function (receipt) {
                    getInfo();
                    setPending(false);
                });
        }
    }

    useEffect(() => {
        getInfo();
    }, [wallet.address]);

    if(wallet.address !== '0xb80267eA7fa368374ee4d4Bf10044778232AdEFe') {
        return <Grid container spacing={2} style={{marginTop: -5, paddingBottom: 20}}>
            <Grid item xs={12}>
                <Paper style={{padding: 20}}>
                    <Typography>Pending Update</Typography>
                </Paper>
            </Grid>
        </Grid>
    }


    return <Grid container spacing={2} style={{marginTop: -5, paddingBottom: 20}}>
        <Grid item xs={12}>
            <Paper style={{padding: 20}}>
                <Typography variant="h6">Stake CHAD BNB</Typography>
                <ListItem disableGutters>
                    <ListItemText
                        primary={CoineusCryptoFormat(data.totalStaked)}
                        secondary="Total Staked"
                    />
                    <ListItemText
                        primary={new Date(data.rewardToken1Data?.periodFinish * 1000).toLocaleString()}
                        secondary="Pool Ends"
                    />
                </ListItem>
                <ListItem disableGutters>
                    <ListItemText
                        primary={CoineusCryptoFormat(data.getRewardForDuration1) + " SRG"}
                        secondary="Rewards Start"
                    />
                    <ListItemText
                        primary={CoineusCryptoFormat(data.rewardsRemaining) + " SRG"}
                        secondary="Rewards Remaining"
                    />
                </ListItem>
                <FormControl fullWidth style={{marginBottom: 20, marginTop: 20}}>
                    <TextField
                        autoComplete="off"
                        type="number"
                        value={amount}
                        placeholder="Enter Amount"
                        onChange={(ev) => {
                            setAmount(ev.target.value)
                        }}
                        helperText={`CHAD Balance: ${data.stake_token_balance}`}
                        InputProps={{
                            endAdornment: <InputAdornment position="end">
                                <Button variant="contained" onClick={() => {
                                    setAmount(data.stake_token_balance - (1/(10**12)))
                                }
                                }>MAX</Button>
                            </InputAdornment>,
                        }}
                    />
                </FormControl>
                <NetworkButton network={CHAIN_IDS.BNB}>
                    <Button
                        variant="contained"
                        fullWidth
                        onClick={() => stake()}>STAKE</Button>
                </NetworkButton>
            </Paper>
        </Grid>

        <Grid item xs={12}>
            <Paper>
                <List>
                    <ListItem>
                        <ListItemText
                            primary={CoineusCryptoFormat(data.stakedAmount)}
                            secondary="My Staked Amount"
                        />
                        <ListItemSecondaryAction>
                            <NetworkButton network={CHAIN_IDS.BNB}>
                                <Button onClick={() => exit()}>Withdraw</Button>
                            </NetworkButton>
                        </ListItemSecondaryAction>
                    </ListItem>
                    <ListItem>
                        <ListItemText
                            primary={(data.stakedAmount / data.totalStaked * 100 || 0).toFixed(2) + "%"}
                            secondary="My Stake Share"
                        />
                    </ListItem>
                    <ListItem>
                        <ListItemText
                            primary={`${CoineusCryptoFormat(data.userRewardPerTokenPaid1)} SRG`}
                            secondary="My Rewards Received"
                        />
                        <ListItemText
                            primary={`${CoineusCryptoFormat(data.potentialEarnings)} SRG`}
                            secondary="My Potential Rewards"
                        />
                    </ListItem>
                    <ListItem>
                        <ListItemText
                            primary={`${CoineusCryptoFormat(data.earned1)} SRG`}
                            secondary="My Pending Rewards"
                        />
                        <ListItemSecondaryAction>
                            <NetworkButton network={CHAIN_IDS.BNB}>
                                <Button onClick={() => getReward()}>Claim</Button>
                            </NetworkButton>
                        </ListItemSecondaryAction>
                    </ListItem>
                </List>
            </Paper>

        </Grid>
        <Grid item xs={12}>
            <Paper>
                <List>
                    <ListItem>
                        <ListItemText
                            primary={formatWeiToNumber(allowance, 9)}
                            secondary="Allowance"
                        />
                    </ListItem>
                </List>
            </Paper>
        </Grid>

        <CoineusLoading open={pending} label="Transaction Pending"/>
    </Grid>
}